@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

:root{--montserrat:'Montserrat', 'Open Sans', sans-serif;--aileron: 'Aileron', 'Open Sans', sans-serif;--accent: rgb(255, 94, 0)}html,body{box-sizing:border-box;background-color:#fcfcff;overflow-x:hidden}*{padding:0;margin:0;font-family:'Aileron', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;cursor:url(/static/media/cursor.074b2adb.svg),auto !important}.container-fluid{width:87.5%}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}@media (max-width: 768px){.container-fluid{width:100%}}@-webkit-keyframes fadeIn{from{visibility:hidden;opacity:0;-webkit-filter:saturate(0);filter:saturate(0)}to{visibility:visible;opacity:1;-webkit-filter:saturate(1);filter:saturate(1)}}@keyframes fadeIn{from{visibility:hidden;opacity:0;-webkit-filter:saturate(0);filter:saturate(0)}to{visibility:visible;opacity:1;-webkit-filter:saturate(1);filter:saturate(1)}}

.navbar{padding:1rem 1rem;background-color:#fcfcff;border-bottom:.5px ridge transparent}.navbar-brand{text-transform:uppercase;color:rgba(0,0,0,0.9);font-weight:600;font-size:1.2rem;letter-spacing:-1px;transform:scaleX(1.2)}.bar1,.bar2,.bar3{width:35px;height:2px;background-color:#333;margin:6px 0;transition:0.3s;transform-origin:13.5px 0px}.bar1{transform:rotate(-45deg) translate(-9px, 6px)}.bar2{width:30px;margin-left:5px;transform:scaleX(0);opacity:0}.bar3{transform:rotate(45deg) translate(-8px, -8px)}.collapsed .bar1{transform:none}.collapsed .bar2{opacity:1;transform:scaleX(1)}.collapsed .bar3{transform:none}.nav-link{text-transform:uppercase;font-weight:400;font-size:1.1rem;color:rgba(0,0,0,0.6) !important;letter-spacing:-1px;text-align:center;-webkit-transform:scaleX(1.2);-moz-tranform:scaleX(1.2);transform:scaleX(1.2);transition:.1s}.nav-link:not(:nth-last-of-type(1)){margin-right:5rem}.nav-link:hover{color:#000 !important}.navbar-toggler{border:none}@media (max-width: 992px){.navbar-brand{margin-left:1rem}.navbar-collapse{padding-top:15vh}.nav-link{width:75%;margin:0 auto !important;padding:4vh .5rem}}

.hero{background-color:transparent;padding-top:calc(5rem + 10vh);padding-bottom:5vh}.hero__heading__text{opacity:1;-webkit-animation:fadeIn ease-out;animation:fadeIn ease-out;-webkit-animation-duration:1s;animation-duration:1s;font-size:calc(1rem + 5vw);text-transform:uppercase;line-height:0.8;overflow:hidden;font-weight:600;padding-left:0;margin-left:0;letter-spacing:-.5vw;transform-origin:0;transform:scaleX(1.5)}@media (max-width: 800px){.hero__heading__text{letter-spacing:-1vw;font-size:calc(1.75rem + 5vw)}}.hero__arrow{-webkit-animation:fadeIn ease-in;animation:fadeIn ease-in;-webkit-animation-duration:.5s;animation-duration:.5s;margin:calc(2rem + 5vh) 0vh;position:relative}.hero__arrow__icon{transform-origin:top;opacity:0.8;width:5vw;transition:.3s}.hero__arrow__icon:hover{transform:translateY(0.5rem)}@media (max-width: 800px){.hero__arrow__icon{width:10vw;text-align:center}}

.about{padding:2vh inherit;opacity:0;-webkit-animation:fadeIn 1s ease-in-out 1s forwards;animation:fadeIn 1s ease-in-out 1s forwards}.about__heading{text-transform:uppercase;font-family:var(--montserrat);font-size:calc(1rem + 2vw)}.about__heading:hover hr{border-color:var(--accent)}@media (min-width: 801px){.about__heading{transform-origin:bottom;transform:translateY(-5vh)}}@media (max-width: 800px){.about__heading{text-align:center}}.about__heading hr{margin:.5rem 0;transition:.3s}@media (max-width: 800px){.about__heading hr{margin-left:auto;margin-right:auto}}.about__profile{position:relative}.about__profile__img{border-radius:.5rem;-webkit-filter:saturate(0.85);filter:saturate(0.85);width:35vw;max-width:30rem;height:auto;transition:.3s ease-out}.about__profile__img:hover,.about__profile__img:focus{box-shadow:0 .1rem 1rem #fff;-webkit-filter:saturate(1);filter:saturate(1)}@media (min-width: 801px){.about__profile__img{transform-origin:bottom;transform:translateY(-10vh)}}@media (max-width: 800px){.about__profile__img{-webkit-filter:saturate(1);filter:saturate(1);width:75vw;margin:2vh auto}}

